.popular_page{
    padding: 1.5rem 10rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.popular{
    display: flex;
    align-items: center;
    background-color: #ffbe00;
    width: 5px;
    height: 3rem;
}
.popular h1{
    text-transform: capitalize;
    margin-left: 1rem;
    font-size: 2.3rem;
    font-weight: 700;
    white-space: nowrap;
}

.see_all{
    text-decoration: underline;
    text-transform: capitalize;
    color: black;
    font-weight: 600;
}

@media (max-width: 640px) {
    .popular_page{
       width: 100vw;
       padding: 1rem; 
    }
    .popular h1{
        font-size: 1.5rem;
    }
}