.home-page{
    width: 100%;
}

.home-img img{
    width: 100%;
    height: 85vh;
}

.shop-by-level-cont{
    padding: 1rem 5rem;
    display: flex;
    gap: 1rem;
}

.shop-card {
    width: 300px;
    height: 200px;
    perspective: 1000px;

}
  
.shop-card-inner {
    width: 100%;
    height: 100%;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.999s;
}
  
.shop-card:hover .shop-card-inner {
    transform: rotateY(180deg);
}
  
.shop-card-front,
.shop-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
}
  
.shop-card-front {
    background-color: #6A2C70;
    color: #fff;
    display: flex;
    align-items: center;
    border: 10px solid #6A2C70;
    border-radius: 10px;
    justify-content: center;
    font-size: 24px;
    transform: rotateY(0deg);
}
  
.shop-card-back {
    background-color: #ffbe00;
    color: #000;
    display: flex;
    align-items: center;
    border: 10px solid #ffbe00;
    border-radius: 10px;
    justify-content: center;
    font-size: 24px;
    transform: rotateY(180deg);
}

.vdo-section{
    background-image: url('../../public/images/zidikar_bg.svg');
    background-size: cover;
    padding: 1rem 0;
}

.vdos{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.vdos video{
    width: 70rem;
}

@media (max-width: 640px) {
    .home-img img{
        height: 40vh;
    }

    .shop-by-level-cont{
        width: 100%;
        padding: 1rem;
        flex-direction: column;
        align-items: center;
    }

    .vdo-section{
        background-size: cover;
    }

    .vdos{
        width: 100%;
        display: flex;
        justify-content: center;
        padding: 1rem;
    }

    .vdos video{
        width: 100%;
    }
}